import api from "./api";

const userApi = (api) => {
  console.log("api", api);
  const signupUrl = "rocp-project/register";
  const loginUrl = "rocp-project/login";
  const logoutUrl = "api/user/logout";

  const signup = (body) => {
    console.log("body", body);
    return api.post(signupUrl, {
      body: JSON.stringify(body),
    });
  };

  const login = (body) => {
    console.log("body", body);
    return api.post(loginUrl, {
      body: JSON.stringify(body),
    });
  };

  const logout = () => {
    return api.post(logoutUrl);
  };

  return {
    signup,
    login,
    logout,
    ...api,
  };
};

export default userApi(api);
