import React from "react";
import mgmtgif from "./media/mgmtgif.gif";

function EmployeeMgmt() {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        width: "100%",
        padding: "15px 0 0 0",
      }}
    >
      <img
        style={{
          width: "450px",
        }}
        src={mgmtgif}
        alt="Logo"
      />
    </div>
  );
}

export default EmployeeMgmt;
