import React, { useState } from "react";
import accountsApi from "./api/accountsApi";
import "./CreateAccount.css";

const CreateAccount = () => {
  var x = sessionStorage.getItem("user");
  console.log("x", x);
  var y = JSON.parse(x);
  console.log("y", y);

  const [state, setState] = useState({});
  const [createAccount, setCreateAccount] = useState({
    balance: 1000,
    accountStatus: {
      statusId: 2,
      status: "Open",
    },
    accountType: {
      typeId: 1,
      type: "Checking",
    },
    userId: 31,
  });

  console.log("state", state);

  const onInput = (e) => {
    const value =
      e.target.type === "checkbox" ? e.target.checked : e.target.value;
    setCreateAccount({
      [e.target.name]: value,
    });
  };

  const handleSubmit = async (e) => {
    try {
      e.preventDefault();
      const response = await accountsApi.addAccount(createAccount);
      console.log("createAccount", createAccount);
      const data = await response.json();
      console.log("response", data);
      setState(data);
      console.log("state", state);
      //   user.token = token;
      //   console.log("token", token);
      //   window.localStorage.setItem("token", token);
      setCreateAccount(data);
      //   userApi.setAuthorizationHeader(token);
      // history.push("/login");
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <div className="createAccount">
      <div>Hello: {y && y.firstName}</div>

      <form onSubmit={handleSubmit}>
        <label>
          Open Either Checking or Savings :
          <select value={createAccount.value} onChange={onInput}>
            <option value="checking">Checking</option>
            <option value="savings">Savings</option>
          </select>
        </label>
        <input type="submit" value="Submit" />
      </form>

      <div className="accounts">
        <div>Accounts</div>
        <table>
          <tr className="table">
            <th>Account Type</th>
            <th>Balance</th>
            <th>Account Status</th>
          </tr>
          <tr className="tableData">
            <td>{state.accountType && state.accountType.type}</td>
            <td>$ {state.balance && state.balance}</td>
            <td>{state.accountStatus && state.accountStatus.status}</td>
          </tr>
        </table>
      </div>
    </div>
  );
};

export default CreateAccount;
