import React, { createContext, useContext, useState, useEffect } from "react";
import userApi from "../api/userApi";
import { STATUS } from "../constants/status";
const UserContext = createContext();
const UserActionsContext = createContext();
export const useUserContext = () => useContext(UserContext);
export const useUserActionsContext = () => useContext(UserActionsContext);

const UserContextProvider = (props) => {
  console.log("props", props);
  const [user, setUser] = useState(null);
  const [authenticatingStatus, setAuthenticatingStatus] = useState(STATUS.IDLE);

  const _setUser = (user) => {
    console.log("user", user);
    if (user) {
      window.localStorage.setItem("user", JSON.stringify(user));
    } else {
      window.localStorage.removeItem("user");
    }
    setUser(user);
  };

  useEffect(() => {
    try {
      setAuthenticatingStatus(STATUS.PENDING);
      const user = JSON.parse(localStorage.getItem("user"));

      if (user) {
        setUser(user);
        // userApi.setAuthorizationHeader(user.token);
      }
      setAuthenticatingStatus(STATUS.SUCCESS);
    } catch (error) {
      console.log("No used in storage");
      setAuthenticatingStatus(STATUS.ERROR);
    }
  }, []);

  return (
    <UserContext.Provider value={user}>
      <UserActionsContext.Provider value={_setUser}>
        {props.children}
      </UserActionsContext.Provider>
    </UserContext.Provider>
  );
};

export default UserContextProvider;
