import React, { useState } from "react";
import userApi from "./api/userApi";
import { Redirect, Link, useHistory } from "react-router-dom";
// import Auth from "../cookie/Auth";
import "./Login.css";
import { useUserActionsContext } from "./context/UserContext";

const Login = (props) => {
  const setUser = useUserActionsContext();

  const history = useHistory();

  const [userData, setUserData] = useState({
    username: "",
    password: "",
  });

  const [state, setState] = useState({
    errors: [],
  });

  // const [redirect, setRedirect] = useState(false);

  const onInput = (e) => {
    const key = e.target.name;
    const value = e.target.value;
    setUserData((data) => ({ ...data, [key]: value }));
  };

  const displayErrors = (errors) =>
    errors.map((error, i) => <p key={i}>{error.message}</p>);

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await userApi.login(userData);
      const result = await response.json();
      console.log("result", result);

      const resultString = JSON.stringify(result);
      console.log("resultString", resultString);
      localStorage.setItem("user", JSON.stringify(result));
      sessionStorage.setItem("user", JSON.stringify(result));

      localStorage.setItem("time", Date.now());

      const { user } = result;
      // user.token = token;
      setUser(user);
      // history.push("/home");
      // userApi.setAuthorizationHeader(token);

      // if (/*result was succesful*/ result) setRedirect(true);
      // history.goBack();
    } catch (error) {
      console.error(error);
    }
  };

  // return redirect ? (
  //   <Redirect to="/" />
  // ) : (
  return (
    <div>
      <div className="logo"></div>
      <div className="login-page">
        <div className="logo-login">
          <h2 className="logo-header">Login</h2>
        </div>

        <form className="login-form">
          <label className="username">Username:</label>
          <input
            type="text"
            placeholder="Username"
            name="username"
            onChange={onInput}
          />

          <label className="password">Password:</label>
          <input
            type="password"
            placeholder="Password"
            name="password"
            onChange={onInput}
          />

          <button onClick={handleSubmit}>Login</button>
        </form>
        {state.errors.length > 0 && (
          <div className="error">{displayErrors(state.errors)}</div>
        )}
      </div>
    </div>
  );
};

export default Login;
