import React, { useState } from "react";
import userApi from "./api/userApi";
// import '../style/main.css'
import { useHistory, Link } from "react-router-dom";
import "./Register.css";
// import firebase from "../firebase";
import { useUserActionsContext } from "./context/UserContext";

const Register = () => {
  const history = useHistory();
  const setUser = useUserActionsContext();
  const [userData, setUserData] = useState({
    username: "",
    email: "",
    password: "",
    firstName: "",
    lastName: "",
    role: {
      roleId: 3,
      role: "Standard",
    },
  });

  console.log("userData", userData);

  const onInput = (e) => {
    const value =
      e.target.type === "checkbox" ? e.target.checked : e.target.value;
    setUserData({
      ...userData,
      [e.target.name]: value,
    });
  };

  const handleSubmit = async (e) => {
    try {
      e.preventDefault();

      const response = await userApi.signup(userData);
      const { user } = await response.json();

      console.log("response", response);

      //   user.token = token;
      //   console.log("token", token);
      //   window.localStorage.setItem("token", token);
      setUser(user);
      //   userApi.setAuthorizationHeader(token);
      history.push("/login");
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div className="signup-page">
      <div className="form">
        <div className="logo-login">
          {/* <img src="/images/engineerapilogo2.png" alt="document logo" /> */}
          <h2 className="logo-header">Register</h2>
        </div>
        <form className="signup-form">
          <label className="name">Username:</label>
          <input
            type="text"
            placeholder="Username"
            name="username"
            onChange={onInput}
            value={userData.username}
          />
          <label className="email">Email:</label>
          <input
            type="email"
            placeholder="email"
            name="email"
            onChange={onInput}
            value={userData.email}
          />

          <label className="password">Password:</label>
          <input
            type="password"
            placeholder="password"
            name="password"
            onChange={onInput}
            value={userData.password}
          />

          <label className="password">First Name:</label>
          <input
            type="text"
            placeholder="First Name"
            name="firstName"
            onChange={onInput}
            value={userData.firstName}
          />
          <label className="password">Last Name:</label>
          <input
            type="text"
            placeholder="password"
            name="lastName"
            onChange={onInput}
            value={userData.lastName}
          />

          <button className="signup-button" onClick={handleSubmit}>
            Create Account
          </button>
        </form>
        <div className="already"></div>
      </div>
    </div>
  );
};

export default Register;
