import React, { useEffect } from "react";
import Header from "./Header";
import HomeSideNav from "./HomeSideNav";
import Kanban from "./Kanban";
import Stonks from "./Stonks";
import EmployeeMgmt from "./EmployeeMgmt";
import EngApi from "./EngApi";
import "./Home.css";
import coverphoto from "./images/cover-new.jpg";
import coverphotoUpside from "./images/cover-new3.jpg";
import profphoto from "./images/profphoto1.png";
import AboutMe from "./AboutMe";
import AOS from "aos";
import mobile from "./images/eapi-mobile.png";
import desktopeapi from "./images/desktopscreenshop2.png";
import "aos/dist/aos.css";
import firebaseIcon from "./images/firebase-48.png";
import reactNative from "./images/reactnative.png";
import nodeIcon from "./images/nodeicon.png";
import materialUi from "./images/material-ui-icon.png";

import nativeIcon from "./images/native.png";
import rcrmdesktop from "./images/rcrmdesktop.png";
import awsicon from "./images/aws.png";
import mongodb from "./images/mongodb-96.png";
import app2 from "./images/app2.png";
import pphoto from "./images/pphoto.jpg";

AOS.init({
  duration: 1200,
});

const Home = () => {
  useEffect(() => {
    AOS.init({
      duration: 1200,
    });
  });
  return (
    <div className="parentHome">
      <Header />
      <div className="home">
        <div className="jum">
          <img className="cover-image" alt="cover photo" src={coverphoto}></img>
          <div className="name1">
            <div data-aos="fade-right" className="title-section01">
              Kevin Kyle
            </div>
            <div data-aos="fade-left" className="title-section02">
              Full Stack Developer💡
            </div>
          </div>

          <img className="prof-image" alt="cover photo" src={pphoto}></img>
        </div>

        <div data-aos="fade-right" className="section2">
          {/* <HomeSideNav /> */}
          <AboutMe />
        </div>

        <div className="jum2 x-center y-center">
          <div id="rcrm-id"></div>
          <div className="secondj">
            <div className="title4" data-aos="flip-left">
              <a href="http://18.225.6.152/accounts" target="_blank">
                <h2>React CRM </h2>
              </a>

              <p className="eapi-summary">
                CRM System adopting the Salesforce methodology of Accounts and
                Opportunities. Opportunities must be assigned to a contact.
                Anyone looking to manage their work can use this app, hosted on
                Heroku, especially if you are managing contacts through a job
                search or sales operation.
                <a
                  href="https://github.com/kevinkyle4/React-CRM"
                  target="_blank"
                >
                  {" "}
                  <i className="fa fa-github fa-lg"></i>
                </a>
              </p>
              <div className="icons">
                <img className="fb1" alt="desktop" src={reactNative}></img>
                <img className="fb1" alt="desktop" src={mongodb}></img>
                <img className="fb1" alt="desktop" src={nodeIcon}></img>
                <img className="fb1" alt="desktop" src={awsicon}></img>
              </div>
            </div>

            <img data-aos="flip-left" className="desktop2" src={app2}></img>
          </div>
        </div>
      </div>

      <div className="jum3">
        <div id="eng-api-id"></div>
        <div className="secondj">
          <div className="title4" data-aos="flip-left">
            <div id="test">
            <a href="#" target="_blank">
              {" "}
              <h2>EngineerAPI </h2>
            </a>

            <p className="eapi-summary">
              Quotations application designed around standardizing generic
              product specs. Contractors can click one button and receive
              automatic quotes from a variety of suppliers, where lowest bids
              are provided in clear order. Check out the Github repo here:{" "}
              <a
                href="https://github.com/kevinkyle4/eng-api-2.0"
                target="_blank"
              >
                {" "}
                <i className="fa fa-github fa-lg"></i>
              </a>
            </p>
            <img className="mobile" alt="mobile" src={mobile}></img>
          </div>
          </div>
          <div data-aos="flip-left">
            <img className="desktop" alt="desktop" src={desktopeapi}></img>
            <div className="icons">
              <img className="fb1" alt="desktop" src={reactNative}></img>
              <img className="fb1" alt="desktop" src={nativeIcon}></img>
              <img className="fb1" alt="desktop" src={nodeIcon}></img>

              <img className="fb1" alt="desktop" src={firebaseIcon}></img>

              <img className="fb1" alt="desktop" src={materialUi}></img>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
