import api from "./api";

const opportunitiesApi = (api) => {
  const getAccountsByUserUrl = "rocp-project/accounts/owner";
  const addAccountUrl = "rocp-project/accounts";

  // get Oppys By UserId
  const getAccountsByUserId = (userId) => {
    return api.get(getAccountsByUserUrl + "/" + userId);
  };

  const addAccount = (accountData) => {
    console.log("body", accountData);
    return api.post(addAccountUrl, {
      body: JSON.stringify(accountData),
    });
  };

  return {
    getAccountsByUserId,
    addAccount,
  };
};

export default opportunitiesApi(api);
